var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('layout',{attrs:{"bgColor":"hsl(0,0%,15%)","enableTopOffset":""}},[_c('div',{staticClass:"page-favorite-root",style:(_vm.style)},[_vm._v(" 【原文】 "),_c('br'),_vm._v("初，郑武公娶于申，曰武姜，生庄公及共叔段。庄公寤生，惊姜氏，故名曰寤生，遂恶之。爱共叔段，欲立之，亟请于武公，公弗许。及庄公即位，为之请制。公曰：“制，岩邑也，虢叔死焉；他邑唯命。”请京，使居之，谓之京城大叔。 "),_c('br'),_vm._v("祭仲曰：“都城过百雉，国之害也。先王之制，大都不过参国之一；中，五之一；小，九之一。今京不度，非制也，君将不堪。”公曰：“姜氏欲之，焉辟害？”对曰：“姜氏何厌之有！不如早为之所，无使滋蔓！蔓，难图也。蔓草犹不可除，况君之宠弟乎！”公曰：“多行不义，必自毙。子姑待之。” "),_c('br'),_vm._v("既而大叔命西鄙北鄙贰于己。公子吕曰：“国不堪贰，君将若之何？欲与大叔，臣请事之。若弗与，则请除之，无生民心。”公曰：“无庸，将自及。”大叔又收贰以为己邑，至于廪延。子封曰：“可矣！厚将得众。”公曰：“不义不暱，厚将崩。” "),_c('br'),_vm._v("大叔完聚，缮甲兵，具卒乘，将袭郑。夫人将启之。公闻其期，曰：“可矣！”命子封帅车二百乘以伐京。京叛大叔段，段入于鄢。公伐诸鄢。五月辛丑，大叔出奔共。 "),_c('br'),_vm._v("书曰：“郑伯克段于鄢。”段不弟，故不言弟；如二君，故曰克；称郑伯，讥失教也；谓之郑志，不言出奔，难之也。 "),_c('br'),_vm._v("遂置姜氏于城颖，而誓之曰：“不及黄泉，无相见也！”既而悔之。颖考叔为颖谷封人，闻之，有献于公。公赐之食，食舍肉。公问之。对曰：“小人有母，皆尝小人之食矣，未尝君之羹，请以遗之。”公曰：“尔有母遗，繄我独无！”颖考叔曰：“敢问何谓也？”公语之故，且告之悔。对曰：“君何患焉！若阙地及泉，隧而相见，其谁曰不然？”公从之。公入而赋：“大隧之中，其乐也融融！”姜出而赋：“大隧之外，其乐也洩洩！”遂为母子如初。 "),_c('br'),_vm._v("君子曰：“颖考叔，纯孝也，爱其母，施及庄公。《诗》曰：‘孝子不匮，永锡尔类。’其是之谓乎？” "),_c('br'),_c('br'),_vm._v("【注释】 "),_c('br'),_vm._v("从前，郑武公在申国娶了一妻子，叫武姜，她生下庄公和共叔段。庄公出生时脚先出来，武姜受到惊吓，因此给他取名叫“寤生”，很厌恶他。武姜偏爱共叔段，想立共叔段为世子，多次向武公请求，武公都不答应。到庄公即位的时候，武姜就替共叔段请求分封到制邑去。庄公说：“制邑是个险要的地方，从前虢叔就死在那里，若是封给其它城邑，我都可以照吩咐办。”武姜便请求封给京邑，庄公答应了，让他住在那里，称他为京城太叔。 "),_c('br'),_vm._v("大夫祭仲说：“分封的都城如果超过三百方丈，那就会是国家的祸害。先王的制度规定：国内最大的城邑不能超过国都的三分之一，中等的不得超过它的五分一，小的不能超过它的九分之一。现在，京邑的城墙不合规定，这不是先王的制度，这样下去您将会控制不住的。”庄公说：“姜氏想要这样，我怎能躲开这种祸害呢？”祭仲回答说：“姜氏哪有满足的时候！不如及早处置，别让祸根滋长蔓延，一滋长蔓延就难办了。蔓延开来的野草还不能铲除干净，何况是您受宠爱的弟弟呢？”庄公说：“多做不义的事情，必定会自己垮台，你姑且等着瞧吧。” "),_c('br'),_vm._v("过了不久，太叔段使原来属于郑国的西边和北边的边邑也属于自己。公子吕说：“国家不能使土地有两属的情况，现在您打算怎么办？您如果打算把郑国交给太叔，那么我就去服事他；如果不给，那么就请除掉他，不要使人民产生两属的心理。”庄公说：“不用除掉他，他自己将要遭到灾祸的。”太叔又把两属的边邑改为自己统辖的地方，一直扩展到廪延。子封说：“可以行动了！土地扩大了，他将得到老百姓的拥护。”庄公说：“多行不义之事，别人就不会亲近他，土地虽然扩大了，他也会垮台的。” "),_c('br'),_vm._v("太叔修治城廓，聚集百姓，修整盔甲武器，准备好兵马战车，将要偷袭郑国。武姜打算开城门作内应。庄公打听到公叔段偷袭的时候，说：“可以出击了！”命令子封率领车二百乘，去讨伐京邑。京邑的人民背叛共叔段，共叔段于是逃到鄢城。庄公又追到鄢城讨伐他。五月辛丑那一天，太叔段逃到共国。 "),_c('br'),_vm._v("《春秋》记载道：“郑伯克段于鄢。”共叔段不遵守做弟弟的本分，所以不说他是庄公的弟弟；兄弟俩如同两个国君一样，所以用“克”字；称庄公为“郑伯”，是讥讽他对弟弟失教；赶走共叔段是出于郑庄公的本意，便不写共叔段自动出奔，这么处理含有责难郑庄公的意思。 "),_c('br'),_vm._v("庄公就把武姜安置在城颖，并且发誓说：“不到地下泉水（不到死后埋在地下），不再见面！”过了些时候，庄公又后悔了。有个叫颖考叔的，是颖谷管理疆界的官吏，听到这件事，就把贡品献给郑庄公。庄公赐给他饭食。颖考叔在吃饭的时候，把肉留着。庄公问他为什么这样。颖考叔答道：“小人有个老娘，我吃的东西她都尝过，只是从未尝过君王的肉羹，请让我带回去送给她吃。”庄公说：“你有个老娘可以孝敬，唉，唯独我就没有！”颖考叔说：“请问您这是什么意思？”庄公把原因告诉了他，还告诉他后悔的心情。颖考叔答道：“您有什么担心的！只要挖一条地道，挖出了泉水，从地道中想见，谁还说您违背了誓言？”庄公依了他的话。庄公走进地道去见武姜，赋诗道：“大隧之中相见啊，多么和乐相得啊！”武姜走出地道，赋诗道：“大隧之外相见啊，多么舒畅快乐啊！”从此，他们恢复了从前的母子关系。 "),_c('br'),_vm._v("君子说：“颖考叔是位真正的孝子，他不仅孝顺自己的母亲，而且把这种孝心推广到郑伯身上。《诗经·既醉》篇说：‘孝子不断地推行孝道，永远能感化你的同类。’大概就是对颖孝叔这类纯孝而说的吧？” ")])])}
